const script = `"Special" Containment Procedures: The threat posed by SCP-3004 has remained neutralized. Containment of SCP-3004 is focused solely on interception of artifacts that point toward the existence of the Cetlaidi. To that end, archaeological sites in Ireland and Wales should be monitored closely. Artifacts are to be confiscated, cataloged, and incinerated. All SCP-3004 related matters are to be dealt with by the Theology Department. Director Clark is to be notified immediately if any events thought to be SCP-3004 related occur. SCP-3004-1 is believed to have been destroyed or otherwise deterred from the material plane and no longer requires containment. Any information found regarding the existence of the Cicadetta luculenta is to be destroyed and civilians involved amnesticized, due to its connection to SCP-3004. Description: SCP-3004 refers to a series of anomalous events linked to the rituals of a Druidic cult known as the Cetlaidi, or the "Singers." They were located primarily in Ireland during the fifteenth to early nineteenth century. It is known that their worship revolved around the life cycle of the now extinct Cicadetta luculenta.1 Due to the combined actions of various antecedent groups to the Foundation (including the Vatican's Congregation for Otherworldly Acts and the Royal Society for the Imprisonment of Abnormalcy), all knowledge of SCP-3004 and the Singers was destroyed in order to contain SCP-3004-1. As SCP-3004 was thus "destroyed", the SCP-3004-1 entity ceased all activity. SCP-3004 events centered around imagery associated with the rites of the Singers. They occurred most commonly during a celebration surrounding a child's entrance into adulthood which was signified by the first loss of primary teeth. These events were theorized to lead to the entrance of their deity, dubbed SCP-3004-1, into local time-space. SCP-3004 events are understood to have been the result of SCP-3004-1 attempting to act upon the material world. It is unclear as to when SCP-3004-1 began to react to the beliefs of the Singers. While there are signs of varied SCP-3004 events, there are few reports that still exist. Artwork relating to SCP-3004 events show Cicadetta luculenta coming from the facial orifices, causing asphyxiation in many. A written record details the sighting of an anomalously large instance of Cicadetta luculenta (believed to be SCP-3004-1) appearing in the sky, the sight of which caused painful boils in those who witnessed it, along with symptoms resembling radiation sickness. Despite this, the Singers were said to have continued their rituals in the face of SCP-3004 events, which was seen as necessitating their destruction. SCP-3004-1 was an entity which interacted with the Singers through their rituals, causing SCP-3004 events. Little is known about the nature of SCP-3004-1. It is theorized to have been a thought form or an extradimensional being that, through happenstance, responded to the rituals of the Singers. There is no data that exists to point toward SCP-3004-1 being anything greater than a Level-3 Pistiphage2 Entity.`;

